import React from "react";
import * as locales from "@material-ui/core/locale";
import { ThemeProvider, CssBaseline } from "@material-ui/core";

import createLightTheme from "../themes/createLightTheme";
import createDarkTheme from "../themes/createDarkTheme";
import { useLocalizationContext } from "./useLocalization";

const ThemeContext = React.createContext();

export function withTheme(Component) {
  return function WithTheme(props) {
    const [isLightThemeOn, setIsLightThemeOn] = React.useState(true);

    const { language } = useLocalizationContext();

    const locale = locales[language.code];

    const theme = isLightThemeOn
      ? createLightTheme(locale)
      : createDarkTheme(locale);

    return (
      <ThemeContext.Provider
        value={{
          isLightThemeOn,
          useLightTheme: () => setIsLightThemeOn(true),
          useDarkTheme: () => setIsLightThemeOn(false),
        }}
      >
        <ThemeProvider theme={theme}>
          <CssBaseline />
          <Component {...props} />
        </ThemeProvider>
      </ThemeContext.Provider>
    );
  };
}

export default function useAppTheme() {
  return React.useContext(ThemeContext);
}
