import { createMuiTheme } from "@material-ui/core";

export default function createLightTheme(locale) {
  return createMuiTheme(
    {
      palette: {
        primary: {
          main: "#2196f3",
        },
        secondary: {
          main: "#e91e63",
        },
        background: {
          default: "#ffffff",
        },
      },
    },
    locale,
  );
}
