import {
  Container,
  Box,
  Grid,
  Avatar,
  Typography,
  TextField,
  FormControlLabel,
  Checkbox,
  Button,
  Link,
  InputAdornment,
  IconButton,
  makeStyles,
} from "@material-ui/core";

import { Formik, Form } from "formik";

const SPACING = 2;

export default function UserProfile() {
  const initialValues = {
    firstName: "",
    middleName: "",
    lastName: "",
    dateOfBirth: "",
    jobTitle: "",
    email: "",
    address: "",
  };

  function handleSubmit(values, formik) {}

  return (
    <Formik initialValues={initialValues} onSubmit={handleSubmit}>
      {({
        values,
        errors,
        touched,
        isSubmitting,
        handleChange,
        handleBlur,
      }) => (
        <Form>
          <Grid item container direction="column" spacing={SPACING}>
            <Grid item container spacing={SPACING}>
              <Grid item xs>
                <TextField
                  fullWidth
                  id="firstName"
                  label=""
                  value={values.firstName}
                  variant="outlined"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </Grid>

              <Grid item xs>
                <TextField
                  fullWidth
                  id="lastName"
                  label=""
                  value={values.lastName}
                  variant="outlined"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </Grid>
            </Grid>

            <Grid item container spacing={SPACING}>
              <Grid item xs>
                <TextField
                  fullWidth
                  id="middleName"
                  label=""
                  value={values.middleName}
                  variant="outlined"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </Grid>

              <Grid item xs>
                <TextField
                  fullWidth
                  id="dateOfBirth"
                  name="dateOfBirth"
                  label=" "
                  value={values.dateOfBirth}
                  type="date"
                  variant="outlined"
                  InputLabelProps={{
                    shrink: true,
                  }}
                />
              </Grid>
            </Grid>

            <Grid item>
              <TextField
                fullWidth
                disabled
                id="jobTitle"
                label=""
                value={values.jobTitle}
                variant="outlined"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </Grid>

            <Grid item>
              <TextField
                fullWidth
                id="email"
                label=" "
                value={values.email}
                variant="outlined"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </Grid>

            <Grid item>
              <TextField
                fullWidth
                id="address"
                label=""
                value={values.address}
                variant="outlined"
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </Grid>

            <Grid item>
              <Box display="flex" justifyContent="flex-end">
                <Button type="sumbit" variant="contained" color="primary">
                  Save
                </Button>
              </Box>
            </Grid>
          </Grid>
        </Form>
      )}
    </Formik>
  );
}
