import {
  Container,
  Box,
  Grid,
  Avatar,
  Typography,
  TextField,
  FormControlLabel,
  Checkbox,
  Button,
  Link,
  InputAdornment,
  IconButton,
  makeStyles,
} from "@material-ui/core";
import FaceIcon from "@material-ui/icons/Face";

import Header from "common/components/Header/Header";
import Breadcrumbs from "common/components/Breadcrumbs/Breadcrumbs";
import useLocalization from "common/hooks/useLocalization";
import useAuth from "common/hooks/useAuth";
import UserProfile from "./UserProfile/UserProfile";

const useUserInfoStyles = makeStyles((theme) => ({
  avatar: {
    width: theme.spacing(25),
    height: theme.spacing(25),
    borderRadius: theme.spacing(1),
  },
  userName: {
    fontWeight: "bold",
  },
  email: {
    color: theme.palette.text.hint,
  },
  button: {
    backgroundColor: theme.palette.primary.main,
    color: theme.palette.text.primary,
  },
}));

function UserInfo() {
  const classes = useUserInfoStyles();
  const auth = useAuth();

  return (
    <>
      <img
        className={classes.avatar}
        src={auth.user.avatar}
        alt="avatar"
        loading="lazy"
      />

      <Box mt={2}>
        <Typography className={classes.userName} variant="h6">
          {auth.user.firstName}
        </Typography>
      </Box>

      <Box>
        <Typography className={classes.email} variant="subtitle1">
          user@mail.ru
        </Typography>
      </Box>
    </>
  );
}

const useStyles = makeStyles((theme) => ({
  root: { minWidth: theme.breakpoints.values.md },
  body: {
    maxWidth: theme.breakpoints.values.md,
    height: "100vh",
    margin: "auto",
  },
}));

export default function UserSettings() {
  const classes = useStyles();
  const l = useLocalization();

  return (
    <div className={classes.root}>
      <Header title={l("userProfile.page")} Icon={FaceIcon} />

      <div className={classes.body}>
        <Box mt={2}>
          <Breadcrumbs
            pages={[
              { name: l("userProfile.home"), to: "/" },
              { name: l("userProfile.settings") },
            ]}
          />
        </Box>

        <Box mt={2}>
          <Grid container>
            <Grid item xs={3}>
              <UserInfo />
            </Grid>

            <Grid item xs={9}>
              <UserProfile />
            </Grid>
          </Grid>
        </Box>
      </div>
    </div>
  );
}
